function lighten(idx) {
  let lightenPixelColor = canvasData[canvasId][idx];
  if (lightenPixelColor === defaultColor) {
    return false;
  }
  const hexColor = parseInt(lightenPixelColor.slice(1), 16);
  const lightenForce = Math.round(2.55 * amount);
  const redChannel = (hexColor >> 16) + lightenForce;
  const blueChannel = (hexColor >> 8 & 0x00FF) + lightenForce;
  const greenChannel = (hexColor & 0x0000FF) + lightenForce;
  lightenPixelColor = `#${(0x1000000 + (redChannel < 255 ? redChannel < 1 ? 0 : redChannel : 255) * 0x10000 + (blueChannel < 255 ? blueChannel < 1 ? 0 : blueChannel : 255) * 0x100 + (greenChannel < 255 ? greenChannel < 1 ? 0 : greenChannel : 255)).toString(16).slice(1)}`;
  canvasData[canvasId][idx] = lightenPixelColor;
  paintedPixels.push(idx);
}

function allowDrop(ev) {
  ev.preventDefault();
}

function drag(ev) {
  ev.dataTransfer.setData('src', ev.target.id);
}

function drop(ev) {
  ev.preventDefault();
  const dropTarget = ev.path[1];
  const dragTargetId = ev.dataTransfer.getData('src');
  const dragTarget = document.getElementById(dragTargetId);
  const tmp = document.createElement('span');
  tmp.classList.add('hide');
  dropTarget.parentNode.insertBefore(tmp, dropTarget);
  dragTarget.parentNode.insertBefore(dropTarget, dragTarget);
  tmp.replaceWith(dragTarget);
}

function handleMouseDown(e) {
  const leftMouse = 1;
  const rightMouse = 3;
  if (e.which === rightMouse) {
    currentColor = secondColor.value;
  }
  if (e.which === leftMouse) {
    currentColor = firstColor.value;
  }
  clickAndDrag = true;
  mouseDown = true;
  mouseDownAt = Date.now();
}

function swapColor() {
  const swapPixelColor = pixelColor;
  for (let i = 0; i < canvasData[canvasId].length; i += 1) {
    if (canvasData[canvasId][i] === swapPixelColor) {
      canvasData[canvasId][i] = currentColor;
      paintedPixels.push(i);
    }
  }
}
function dithering(idx) {
  if (paintedPixels.includes(idx)) { return false; }
  if (clickAndDrag) {
    if (startX % 2 === 0 && startY % 2 === 0) {
      canvasData[canvasId][idx] = firstColor.value;
    }
    if (startX % 2 === 0 && startY % 2 !== 0) {
      canvasData[canvasId][idx] = secondColor.value;
    }
    if (startX % 2 !== 0 && startY % 2 === 0) {
      canvasData[canvasId][idx] = secondColor.value;
    }
    if (startX % 2 !== 0 && startY % 2 !== 0) {
      canvasData[canvasId][idx] = firstColor.value;
    }
  }
  paintedPixels.push(idx);
}

function setData(idx, color) {
  if (paintedPixels.includes(idx)) { return false; }
  pixelColor = canvasData[canvasId][idx];
  if (!clickAndDrag) {
    if (color !== pixelColor) {
      canvasData[canvasId][idx] = color;
    } else {
      canvasData[canvasId][idx] = defaultColor;
    }
  } else { canvasData[canvasId][idx] = color; }
  paintedPixels.push(idx);
}

function coordsToIdx(x, y) {
  const col = Math.floor(x / boxSideLength);
  const row = Math.floor(y / boxSideLength);
  startY = row;
  startX = col;
  mouseCoords.innerHTML = `[${selectedSize.value}x${selectedSize.value}] X:${startX} Y:${startY}`;
  return row * numRows + col;
}

function colorPicker(idx) {
  firstColor.value = canvasData[canvasId][idx];
}

