function handleMouseUp(e) {
  if (clickAndDrag) {
    const x = e.offsetX;
    const y = e.offsetY + 10;
    if ((x >= width || x <= 0) || (y >= heigth || y <= 0)) { return false; }
    if (currentTool === 'eraser') {
      setData(coordsToIdx(x, y), defaultColor);
    }
    if (currentTool === 'pen') {
      setData(coordsToIdx(x, y), currentColor);
    }
    if (currentTool === 'dither') {
      dithering(coordsToIdx(x, y), x, y);
    }
    if (currentTool === 'mirror') {
      setData(coordsToIdx(x, y), currentColor);
      setData(coordsToIdx(x, width - y), currentColor);
    }
  }
  mouseDown = false;
  mouseDownAt = null;
  clickAndDrag = false;
  return e;
}

function mouseMoveCoords(e) {
  const x = e.offsetX;
  const y = e.offsetY;
  coordsToIdx(x, y);
}
function handleMouseMove(e) {
  if (!mouseDown) { return false; }
  if ((Date.now() - mouseDownAt) > dragDelayMs) {
    const x = e.offsetX;
    const y = e.offsetY + 10;
    clickAndDrag = true;
    if ((x >= width || x <= 0) || (y >= heigth || y <= 0)) { return false; }
    if (currentTool === 'eraser') {
      setData(coordsToIdx(x, y), defaultColor);
    }
    if (currentTool === 'pen') {
      setData(coordsToIdx(x, y), currentColor);
    }
    if (currentTool === 'dither') {
      dithering(coordsToIdx(x, y), x, y);
    }
    if (currentTool === 'mirror') {
      setData(coordsToIdx(x, y), currentColor);
      setData(coordsToIdx(x, width - y), currentColor);
    }
  }
  return e;
}

function idxToRowCol(idx) {
  const row = Math.floor(idx / numRows);
  const col = idx % numCols;
  return { row, col };
}


function reOutline(row, col) {
  ctx.linewidth = 0.5;
  ctx.setTransform(1, 0, 0, 1, 0, 0);
  ctx.translate(0.5, 0.5);
  ctx.beginPath();
  ctx.moveTo(col * boxSideLength, row * boxSideLength);
  ctx.lineTo(col * boxSideLength, (row + 1) * boxSideLength);
  ctx.stroke();
  ctx.lineTo((col + 1) * boxSideLength, (row + 1) * boxSideLength);
  ctx.stroke();
  ctx.lineTo((col + 1) * boxSideLength, row * boxSideLength);
  ctx.stroke();
  ctx.lineTo(col * boxSideLength, row * boxSideLength);
  ctx.stroke();
  ctx.closePath();
}
const isValidCol = col => col >= 0 && col <= numCols - 1;
const isValidRow = row => row >= 0 && row <= numRows - 1;

function colorBox(box, color) {
  const { row, col } = box;
  if (!isValidCol(col) || !isValidRow(row)) { return false; }
  ctx.fillStyle = color || currentColor;
  ctx.clearRect(col * boxSideLength, row * boxSideLength, boxSideLength, boxSideLength);
  ctx.beginPath();
  ctx.fillRect(col * boxSideLength, row * boxSideLength, boxSideLength, boxSideLength);
  ctx.closePath();
  reOutline(row, col);
}


function refreshCanvas() {
  for (let i = 0; i < canvasData[canvasId].length; i += 1) {
    paintedPixels.push(i);
  }
}

function resetCanvas() {
  for (let i = 0; i < canvasData[canvasId].length; i += 1) {
    canvasData[canvasId][i] = defaultColor;
    paintedPixels.push(i);
  }
  const preview = document.getElementById(`preview${canvasId}`);
  preview.style.backgroundImage = '';
}


function matchStartColor(pixelPos) {
  const colorToMatch = canvasData[canvasId][pixelPos];
  return (colorToMatch === startColor);
}

function colorPixel(pixelPos) {
  canvasData[canvasId][pixelPos] = currentColor;
}

function paintBucket() {
  const pixelStack = [[startX, startY]];
  while (pixelStack.length) {
    let pixelPos;
    let reachLeft;
    let reachRight;
    const newPos = pixelStack.pop();
    const x = newPos[0];
    let y = newPos[1];

    pixelPos = y * numRows + x;
    startColor = canvasData[canvasId][pixelPos];
    if (startColor === currentColor) {
      break;
    }
    while (y-- >= 0 && matchStartColor(pixelPos)) {
      pixelPos -= numRows;
    }
    pixelPos += numRows;
    ++y;
    reachLeft = false;
    reachRight = false;
    while (y++ < numCols - 1 && matchStartColor(pixelPos)) {
      colorPixel(pixelPos);

      if (x > 0) {
        if (matchStartColor(pixelPos - 1)) {
          if (!reachLeft) {
            pixelStack.push([x - 1, y]);
            reachLeft = true;
          }
        } else if (reachLeft) {
          reachLeft = false;
        }
      }

      if (x < numRows - 1) {
        if (matchStartColor(pixelPos + 1)) {
          if (!reachRight) {
            pixelStack.push([x + 1, y]);
            reachRight = true;
          }
        } else if (reachRight) {
          reachRight = false;
        }
      }

      pixelPos += 32;
    }
  }
  refreshCanvas();
}

function handleClick(e) {
  const x = e.offsetX;
  const y = e.offsetY + 10;
  if ((x >= width || x <= 0) || (y >= heigth || y <= 0)) { return false; }
  if (currentTool === 'pen') {
    setData(coordsToIdx(x, y), currentColor);
  }
  if (currentTool === 'dither') {
    dithering(coordsToIdx(x, y), x, y);
  }
  if (currentTool === 'mirror') {
    setData(coordsToIdx(x, y), currentColor);
    setData(coordsToIdx(x, width - y), currentColor);
  }
  if (currentTool === 'eraser') {
    setData(coordsToIdx(x, y), defaultColor);
  }
  if (currentTool === 'bucket') {
    coordsToIdx(x, y);
    paintBucket();
  }
  if (currentTool === 'colorswap') {
    setData(coordsToIdx(x, y), currentColor);
    swapColor();
  }
  if (currentTool === 'picker') {
    colorPicker(coordsToIdx(x, y));
  }
  if (currentTool === 'lighten') {
    lighten(coordsToIdx(x, y));
  }
  return e;
}

function grabCanvas(outputWidth, el) {
  const elementId = el.id;
  const canvasElementId = el.dataset.previewposition;
  const preview = document.getElementById(elementId);
  const previewImg = document.createElement('canvas');
  previewImg.height = outputWidth; // px
  previewImg.width = outputWidth;
  previewImg.classList.add('outputGif');
  const previewImgCtx = previewImg.getContext('2d');
  previewImgCtx.imageSmoothingEnabled = false;
  const cellDim = Math.ceil(outputWidth / numCols);


  function colorScaledBox(box, color) {
    const { row, col } = box;
    if (!isValidCol(col) || !isValidRow(row)) {
      return false;
    }
    previewImgCtx.fillStyle = color || currentColor;
    previewImgCtx.clearRect(col * cellDim, row * cellDim, cellDim, cellDim);
    previewImgCtx.beginPath();
    previewImgCtx.fillRect(col * cellDim, row * cellDim, cellDim, cellDim);
    previewImgCtx.closePath();
  }

  for (let i = 0; i < canvasData[canvasElementId].length; i += 1) {
    const row = Math.floor(i / numRows);
    const col = i % numCols;
    const color = canvasData[canvasElementId][i];
    if (color === defaultColor) {
      colorScaledBox({ row, col }, defaultColor);
    } else { colorScaledBox({ row, col }, color); }
  }
  img = previewImg.toDataURL('image/png');
  preview.style.backgroundImage = `url(${img})`;
  const prevCanvas = preview.lastChild;
  prevCanvas.replaceWith(previewImg);
}

function apngEncoder() {
  const imgs = [];
  const size = selectedSize.value;
  const apngDelay = seconds / fps;
  const frames = document.querySelectorAll('.outputGif');
  const framesCount = frames.length;
  frames.forEach((el) => {
    const apngCtx = el.getContext('2d');
    const apngArray = apngCtx.getImageData(0, 0, size, size).data;
    imgs.push(apngArray.buffer);
  });
  const delay = new Array(framesCount).fill(apngDelay);
  const apng = UPNG.encode(imgs, size, size, 0, delay);
  if (saveToGdrive) {
    fileContent = apng;
  } else { download(apng, 'newPiskel.apng', 'apng'); }
}

function gifEncoder() {
  const outputWidth = selectedSize.value;
  const gifDelay = seconds / fps;
  gif = new GIF({
    workers: 2,
    quality: 10,
    width: outputWidth,
    height: outputWidth,
    workerScript: './gif.worker.js',
  });
  document.querySelectorAll('.outputGif').forEach((el) => {
    gif.addFrame(el, { delay: gifDelay });
  });
  gif.on('finished', (blob) => {
    const resultGif = URL.createObjectURL(blob);
    downloadAsGif.setAttribute('href', resultGif);
    downloadAsPiskel.setAttribute('href', resultGif);
    animationWindow.style.backgroundImage = `url(${resultGif})`;
    fileContent = blob;
  });
  gif.render();
}

function getCanvasAndContext() {
  canvas = document.getElementById(canvasId);
  ctx = canvas.getContext('2d');
  ctx.imageSmoothingEnabled = false;
  canvas.width = numCols * boxSideLength + 1;
  canvas.heigth = numRows * boxSideLength + 1;
}

