function addCanvas() {
  canvas.style.visibility = 'hidden';
  totalCanvases += 1;
  canvasId = totalCanvases;
  const previewImg = document.createElement('canvas');
  previewImg.classList.add('outputGif');
  const newCanvas = document.createElement('canvas');
  const buttonsWrap = document.createElement('div');
  const buttonRemove = document.createElement('button');
  const removeImg = document.createElement('img');
  removeImg.setAttribute('src', 'img/frame-recyclebin.png');
  buttonRemove.classList.add('buttonRemove');
  buttonsWrap.classList.add('buttons');
  const cloneImg = document.createElement('img');
  cloneImg.setAttribute('src', 'img/frame-duplicate.png');
  const buttonClone = document.createElement('button');
  buttonClone.classList.add('buttonClone');
  const newPreview = document.createElement('li');

  newCanvas.height = width + 1; //  px
  newCanvas.width = width + 1;
  newCanvas.classList.add('editor');
  newPreview.classList.add('preview-item');
  newPreview.id = `preview${canvasId}`;
  newPreview.setAttribute('draggable', 'true');
  newPreview.dataset.previewposition = canvasId;
  newCanvas.id = totalCanvases;
  canvasContainer.appendChild(newCanvas);

  previewList.appendChild(newPreview);
  buttonRemove.appendChild(removeImg);
  buttonClone.appendChild(cloneImg);
  buttonsWrap.appendChild(buttonClone);
  buttonsWrap.appendChild(buttonRemove);
  newPreview.appendChild(buttonsWrap);
  newPreview.appendChild(previewImg);
}

function addNewFrame() {
  addCanvas();
  canvasData.push(Array.from(emptyCanvasData));
  getCanvasAndContext();
  addListeners();
  refreshCanvas();
  getUserImageParameters();
}

function reorderFramePositions() {
  let newId = 0;
  document.querySelectorAll('.preview-item').forEach((el) => {
    el.dataset.previewposition = `${newId += 1}`;
    el.setAttribute('id', `preview${newId}`);
  });
  newId = 0;
  document.querySelectorAll('.editor').forEach((el) => {
    el.setAttribute('id', `${newId += 1}`);
  });
}


function removeFrame(event) {
  event.stopPropagation();
  const selectedCanvas = event.path[3].dataset.previewposition;
  canvasData.splice(+(selectedCanvas), 1);
  const removePreview = document.getElementById(`preview${selectedCanvas}`);
  const currentCanvas = document.getElementById(`${selectedCanvas}`);
  const prevSiblingId = removePreview.previousElementSibling.dataset.previewposition;
  let nextSiblingId;
  if (removePreview.nextElementSibling) {
    nextSiblingId = removePreview.nextElementSibling.dataset.previewposition;
  }
  currentCanvas.remove();
  removePreview.remove();
  canvasId = +(prevSiblingId) || +(nextSiblingId);
  totalCanvases -= 1;
  reorderFramePositions();
  getCanvasAndContext();
  refreshCanvas();
  canvas.style.visibility = 'visible';
  addListeners();
}

function addCloneFrame(event) {
  event.stopPropagation();
  const selectedCanvas = event.path[3].dataset.previewposition;
  const copy = Array.from(canvasData[selectedCanvas]);
  canvasData.push(copy);
  addCanvas();
  getCanvasAndContext();
  refreshCanvas();
  getUserImageParameters(canvas);
  addListeners();
}

function saveFiles() {
  let saveType;
  let saveName;
  let saveMimeType;

  if (this.id === 'GSaveApng') {
    saveToGdrive = true;
    apngEncoder();
    saveType = 'image/apng';
    saveName = 'imageApng.apng';
    saveMimeType = 'apng';
    saveToGdrive = false;
  }

  if (this.id === 'GSaveGif') {
    gifEncoder();
    saveType = 'image/gif';
    saveName = 'imageGif.gif';
    saveMimeType = 'gif';
  }

  const file = new Blob([fileContent], { type: saveType });
  const metadata = {
    name: saveName,
    mimeType: saveMimeType,
  };
  const accessToken = window.gapi.auth2.getAuthInstance().currentUser.get()
    .getAuthResponse().access_token;
  const form = new FormData();
  form.append('metadata', new Blob([JSON.stringify(metadata)], { type: 'application/json' }));
  form.append('file', file);
  fetch('https://www.googleapis.com/upload/drive/v3/files?uploadType=multipart&fields=id', {
    name: saveName,
    method: 'POST',
    headers: new Headers({ Authorization: `Bearer ${accessToken}` }),
    body: form,
  }).then(res => res.json()).then((val) => {
    if (val.id) {
      alert('Saved on Google Disk');
    } else { alert('Something went wrong :( Check connection / login status'); }
  });
}

function saveFilesGiphy() {
  gifEncoder();
  const saveType = 'image/gif';
  const saveName = 'imageGif.gif';
  const saveMimeType = 'gif';

  const file = new Blob([fileContent], { type: saveType });
  const metadata = {
    name: saveName,
    mimeType: saveMimeType,
  };
  const GiphyapiKey = 'dc6zaTOxFJmzC';
  const form = new FormData();
  form.append('metadata', new Blob([JSON.stringify(metadata)], { type: 'application/json' }));
  form.append('file', file);
  const xhr = new XMLHttpRequest();
  xhr.open('post', `//upload.giphy.com/v1/gifs?api_key=${GiphyapiKey}`);
  xhr.responseType = 'json';
  xhr.onload = () => {
    if (xhr.response.data.id) {
      window.open(`https://media.giphy.com/media/${xhr.response.data.id}/giphy.gif`);
    } else { alert(`Gif upload failed: ${xhr.response.meta.msg}`); }
  };
  xhr.send(form);
}

