// main drawing loop
function drawData() {
  for (let i = 0; i < paintedPixels.length; i += 1) {
    const color = canvasData[canvasId][paintedPixels[i]];
    colorBox(idxToRowCol(paintedPixels[i]), color);
  }
  paintedPixels = [];
  requestAnimationFrame(drawData);
}

function initEditor() {
  getCanvasAndContext();
  resetCanvas();
  addListeners();
}
handleClientLoad();
initEditor();
